          'title': '【ESP8266】桌面摆件',
          'text': '0'
        },
        {
          'path': '/sophomore/Git-Bash.html',
          'image': 'https://www.write-down.tech/usr/themes/handsome/assets/img/sj2/1.jpg',
          'title': '【工具】Git Bash使用',
          'text': '0'
        }
      ]
    },
    {
      icon: 'ChatBubbleLeftRight',
