    'times2',
    '产品经理',
    '人工智能',
    '项目实践',
    '密码学',
    '线程'
  ]
  public getCurrentContent () {
    return this.handles[this.current].title
  }
  public entrust (e: MouseEvent) {
    let dom = e.target as HTMLElement
    while (dom.parentElement !== e.currentTarget) {
      dom = dom.parentElement as HTMLInputElement
    }
    let index = dom.getAttribute('data-index')
    if (index !== null) {
      this.current = parseInt(index)
    }
  }
}
