<template>
  <svg :class="svgClass" :style="{ width: size, height: size, color: color }" aria-hidden="true">
    <use :xlink:href="`#icon-${name}`" />
  </svg>
</template>
<script>
import { defineComponent } from 'vue'

// 雪碧图，根据如下文章配置
// https://blog.csdn.net/m0_66746443/article/details/123381448
export default defineComponent({
  name: 'svg-icon',
  props: {
    name: { type: String, required: true },     // svg 图标名称
    className: { type: String, default: '' },   // 指定的类样式
    size: { type: String, default: '20px' },        // 图标尺寸
    color: { type: String, default: "inherit" }    // 图标颜色
  },
  computed: {  // 计算属性
    svgClass () {
      if (this.className) {
        return 'svg-icon ' + this.className
      } else {
        return 'svg-icon'
      }
    }
  }
})
</script>

<style scoped>
.svg-icon {
  fill: currentColor;
  overflow: hidden;
}
</style>