
import { Options, Vue } from 'vue-class-component'
// import HelloWorld from './components/HelloWorld.vue';

import blogAside from './components/blog-aside/index.vue'
import blogHeader from './components/blog-header/index.vue'
import blogFooter from './components/blog-footer/index.vue'
import blogContent from './components/blog-content/index.vue'

// let showAside: string = 'animation-none'

@Options({
  components: {
    'blog-aside': blogAside,
    'blog-header': blogHeader,
    'blog-footer': blogFooter,
    'blog-content': blogContent
  }
})
export default class App extends Vue {
  showAside = 'animation-none'
  isShowAside = () => {
    return this.showAside
  }
  toogleAside = () => {
    if (this.showAside !== 'animation-show-aside') {
      this.showAside = 'animation-show-aside'
    } else {
      this.showAside = 'animation-hide-aside'
    }
  }
}
